import React from 'react';
import {
	ActionsButtons,
	ConfirmModal,
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import {
	Pagination,
	TableBody,
	TableContainer,
	TableHead,
	WhiteModal
} from 'shared/components/Elements';
import { usePagination, useRouter } from 'shared/hooks';
import { useClientsManagementContext } from '../../context/ClientsManagementContext';
import { ClientGroupEntity } from 'shared/typings/client.types';
import { Link } from 'react-router-dom';
import { useDebounce } from 'use-debounce';
import { useClientGroups } from '../../hooks';
import { EditClientGroup } from '../../components/ClientGroups/EditClientGroup';
import { useTranslation } from 'react-i18next';

function ClientsGroupsInner() {
	const { companyId, isPermitted } = useAppState();
	const { page, handlePage } = usePagination();
	const { searchQuery: query } = useClientsManagementContext();
	const [searchQuery] = useDebounce<string>(query, 500);
	const { match, history } = useRouter();
	const { t } = useTranslation();

	const {
		deleteClientGroupHandler,
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		closeDeletePopup,
		showEditVisible,
		closeEditPopup,
		editVisible
	} = useClientGroups();

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity="clientsGroups"
					name="AllClientsGroups"
					url={`/${companyId}/categories/`}
					params={{
						limit: 10,
						extra: {
							search: searchQuery
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const clientGroups = items as ClientGroupEntity[];
						return (
							<div>
								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '60%' }}>
													{t('group_name')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													{t('clients')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%', textAlign: 'center' }}>
													{t('actions')}
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{clientGroups.map((item) => {
												const { clients_count, name, id } = item;
												return (
													<StyledTableRow key={id}>
														<StyledTableCell>
															<Link to={`${match.path}${item.id}`}>
																<span className="text-primary transition hover:text-primary-dark">
																	{name}
																</span>
															</Link>
														</StyledTableCell>
														<StyledTableCell>{clients_count}</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-center">
																<ActionsButtons.DeleteButton
																	isDisabled={!isPermitted}
																	clickHandler={() => showDeleteVisible(item)}
																/>
																<ActionsButtons.ViewButton
																	clickHandler={() => history.push(`${match.path}${item.id}`)}
																	// isDisabled={!item.owner}
																/>
																<ActionsButtons.EditButton
																	isDisabled={!isPermitted}
																	clickHandler={() => showEditVisible(item)}
																/>
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
								<ConfirmModal
									featureHandler={deleteClientGroupHandler()}
									isOpen={Boolean(deleteVisible)}
									toggleHandler={closeDeletePopup}
									title={`Are you sure to delete the ${deleteVisible?.name}?`}
									hasLoader
									isLoading={deleteLoading}
								/>
								<WhiteModal handleModalOpen={closeEditPopup} open={Boolean(editVisible)}>
									<EditClientGroup onSuccess={closeEditPopup} entity={editVisible!} />
								</WhiteModal>
								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export default function ClientsGroups() {
	return (
		<ErrorBoundary>
			<ClientsGroupsInner />
		</ErrorBoundary>
	);
}
