import React from 'react';
import { useAppState } from 'shared/state';
import { useNotistack } from 'shared/hooks';
import EntityForm from 'modules/entity/forms';
import { FormHeader, Loader } from 'shared/components';
import ClientGroupElements from './ClientGroupElements';
import { ClientGroupEntity } from 'shared/typings/client.types';
import EntityContainer from 'modules/entity/containers';
import { useTranslation } from 'react-i18next';

type Props = {
	onSuccess: () => void;
	entity: ClientGroupEntity;
};

export function EditClientGroup({ onSuccess, entity }: Props) {
	const { t } = useTranslation();
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();

	if (!entity) {
		return null;
	}

	return (
		<div className="max-w-[446px]">
			<EntityContainer.One
				id={entity.id}
				relations={''}
				entity="clientsGroup"
				name="AllClientGroup"
				url={`/${companyId}/categories/${entity.id}/`}
				params={{}}
			>
				{({ item, isFetched }) => {
					const clientGroupEntity = item as ClientGroupEntity;
					return (
						<>
							{!isFetched ? <Loader tiled withOverlay /> : null}
							<EntityForm.Main
								entity="clientsGroups"
								name="AllClientsGroups"
								url={`/${companyId}/categories/${entity.id}/`}
								method="put"
								primaryKey="id"
								sendAsFormData={false}
								normalizeData={(data: any) => data}
								updateData
								fields={[
									{
										name: 'clients',
										onSubmitKey: '',
										required: false,
										type: 'object',
										value: clientGroupEntity.clients
									},
									{
										name: 'name',
										onSubmitKey: '',
										required: true,
										value: clientGroupEntity.name
									}
								]}
								onSuccess={(response, resetForm): any => {
									showNotification({ message: t('update_success') });
									onSuccess();
									resetForm();
								}}
								onError={(error, resetForm): any => {
									const errorText = error?.response?.data?.message || 'Something went wrong';
									showNotification({ message: errorText, variant: 'error' });
								}}
							>
								{({ isSubmitting, values, resetForm }) => {
									if (process.env.NODE_ENV === 'development') {
										console.log('edit client group  values', values);
									}
									return (
										<>
											<FormHeader resetForm={resetForm} title="Edit Client Group" isEditing />
											<>
												<div className={'flex items-center'}>
													<ClientGroupElements
														isSubmitting={isSubmitting}
														onSuccess={onSuccess}
														isEditing
													/>
													{isSubmitting && <Loader tiled withOverlay />}
												</div>
											</>
										</>
									);
								}}
							</EntityForm.Main>
						</>
					);
				}}
			</EntityContainer.One>
		</div>
	);
}
